.Wrapper {
  padding: 2rem;
}

.App {
  -ms-flex-align: center;
      align-items: center;
  color: white;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Avenir Next', sans-serif;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

button.started {
  background: #191654;
  border-color: white white white #43c6ac;
  color: white;
}

.border {
  border: .25rem solid white;
  border-radius: 50%;
  bottom: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.border.started {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  border-top-color: #43c6ac;
}

.Tracker {
  background: rgba(0, 0, 0, .25);
  border-radius: .5rem;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 2rem;
}



