:root {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  background: -webkit-gradient(linear, left top, right top, from(#43c6ac), to(#191654));
  background: -webkit-linear-gradient(left, #43c6ac, #191654);
  background: -o-linear-gradient(left, #43c6ac, #191654);
  background: linear-gradient(to right, #43c6ac, #191654);
  margin: 0;
  padding: 0;
  font-family: 'Avenir Next', sans-serif;
}

dt {
  display: block;
  font-size: 1rem;
  text-align: center;
  margin: 0;
}

dd {
  display: block;
  font-size: 3rem;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

button {
  background: #43c6ac;
  border: none;
  border-radius: 50%;
  color: white;
  font-family: 'Avenir Next', sans-serif;
  font-size: 2rem;
  height: 8rem;
  margin: 1rem;
  outline: 0;
  padding: 1rem;
  position: relative;
  width: 8rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  background: rgba(0, 0, 0, .25);
  border-radius: .25rem;
  margin: 1rem 0;
  padding: 1rem;
}



